let tp = document.querySelector("#text-part");
let string = `/* 我是一名前端新人
* 接下来我演示一下我的前端功底
* 首先我要准备一个div
**/
 #eight-dia{
    width: 200px;
    height: 200px;
    border: 1px solid red; 
 }
 /* 接下来我把div变成一个八卦图
 * 注意看好了
 * 首先,把div变成一个圆
 **/
 #eight-dia {
    border-radius: 50%;
    box-shadow: 0 0 3px rgba(0,0,0,0.5);
    border: none;
 }
 /* 八卦是阴阳相成的
 * 一黑一白
 **/
 #eight-dia {
   background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 51%, rgba(0,0,0,1) 100%);
 }
 /* 加两个神秘的小球 */
 #eight-dia::before{
    width:100px;
    height:100px;
    top: 0;
    left: 50%;
    border-radius: 50%;
    background: #000;
    background: radial-gradient(circle, rgba(255,255,255,1) 24%, rgba(0,0,0,1) 25%);
 }
 #eight-dia::after{
   width:100px;
   height:100px;
   bottom: 0;
   left: 50%;
   border-radius: 50%;
   background: #fff;
   background: radial-gradient(circle, rgba(0,0,0,1) 24%, rgba(255,255,255,1) 25%);
}
   `;
let string2 = ''; 
let n = -1;
let step = ()=>{
    setTimeout(()=> {
     n = n+1;
     if( string[n]=== '\n'){
        string2 = string2 + '</br>';
     } else if(string[n]=== ' '){
        string2 = string2 + '&nbsp;';
     } else{
        string2 = string2 + string[n];
     }
     style.innerHTML = string.substring(0,n);   
     tp.innerHTML = string2;
     window.scrollTo(0, 99999);
     tp.scrollTo(0, 99999);
     if(n<string.length-1){
        step();
     }
        },5);
}
step();